___CSS_LOADER_EXPORT___.push([module.id, `@charset "UTF-8";

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Poppins', Arial, Helvetica, sans-serif;
}

body {
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: aquamarine;
    overflow-x: hidden;
}

main {
    width: 60%;
    height: 60%;
    padding: 20px;
    margin: 50px 0px ;
    background-color: white;
    border-radius: 12px;
    box-shadow: 0px 10px 15px rgba(0, 0, 0, 0.3);
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: space-evenly;
    gap: 10px;
}

.senha-gerada{
    font-size: 2.5em;
    font-weight: 700;
    margin: 10px 0px;
    color: aquamarine;
    letter-spacing: 8px;
}



